////
/// Header Module : default
/// @group header
////

#{ns(header)} {
  @include disable-underline;
  @include disable-list-style;
  @include set-text-margin(0);
  @include set-title-margin(0);
  @include size(100%);
  @include relative;

  @include body {
    &-row {
      @include display-flex(row, center, flex-start);
      @include margin(0 -4v);
      @include padding(6v 0, lg);
    }
  }
}
